<template>
    <div>
        <div class="content" v-loading="loading">
            <div v-for="entrance in entrances" :key="entrance.id" class="entrance-frame">
                <div class="entrance">
                    <div class="title" @click="jump(entrance.id)">{{ entrance.title }}</div>
                    <div class="post-date">{{ handleTime(entrance.time) }}</div>
                </div>
                <div class="introduce">{{ entrance.introduce }}</div>
            </div>
        </div>

        <el-pagination layout="prev, pager, next" 
            :hide-on-single-page="true"
            :total="archivesnum" 
            :current-page.sync="currentPage" 
            :page-size="5"
            @current-change="currentChange">
        </el-pagination>
    </div>
</template>

<script>


export default {
    name: 'Home',
    data() {
        return {
            archivesnum: 0,
            entrances: [],
            // entrances: [
            //     {id: '1', title: 'Hello, World!', date: '2020.2.17', introduce: 'this is a test theme'},
            //     {id: '2', title: '测试帖子', date: '2020.2.17', introduce: '这是一个测试帖子'},
            //     {id: '3', title: '测试帖子', date: '2020.2.17', introduce: '这是一个测试帖子'},
            //     {id: '4', title: '测试帖子', date: '2020.2.17', introduce: '这是一个测试帖子'},
            //     {id: '5', title: '测试帖子', date: '2020.2.17', introduce: '这是一个测试帖子'}
            // ],
            currentPage: 1,
            loading: true
        }
    },
    methods: {
        jump(id) {
            this.$router.push({path: '/archives/' + id});
        },

        currentChange() {
            this.dataRefresh('/' + this.currentPage);
        },

        dataRefresh(url) {
            this.$router.push({
                path: url
            });

            this.loading = true;
            this.$axios({
                type: 'GET',
                url: this.serverAddress + '/archives/entrance?start=' + this.currentPage + '&limit=5'
            }).then((response) => {
                let info = response.data;
                let status = info.status;
                if(status == 200) {
                    this.archivesnum = info.data.total;
                    this.entrances = info.data.items;
                    this.loading = false;
                }
            });
            
        },

        handleTime(time) {
            let date = new Date(parseInt(time * 1000));
            let format = date.getFullYear().toString() + '.' + (date.getMonth() + 1).toString() + '.' + date.getDate().toString();
            return format;
        }
    },
    created() {
        this.currentPage = Number(this.$route.params.id);
        
        this.$emit('tooltip', {
            title: 'About author',
            routeName: 'Author'
        });

        // 创建组件的时候加载主题入口
        this.dataRefresh('/' + this.currentPage);
    }
}
</script>

<style scoped>
    .content {
        margin: 0 auto;
        max-width: 450px;
        text-align: left;
    }

    .entrance-frame {
        margin-bottom: 30px;
        position: relative;
    }
    .entrance-frame .entrance {
        margin-bottom: 5px;
        display: flex;
        justify-content: space-between;
        position: relative;
    }
    .entrance-frame .entrance .title {
        position: relative;
        font-size: 18px;
        font-weight: 600;
        border-bottom: 2px solid rgba(0, 0, 0, 0);
        cursor: pointer;
    }
    .entrance-frame .entrance .title:hover {
        border-bottom: 2px solid;
    }

    .entrance-frame .entrance .post-date {
        font-size: 13px;
        color: #757575;
    }
    .entrance-frame .introduce {
        font-size: 13px;
        color: #757575;
    }
</style>


