<template>
    <div>
        <p class="title">{{ title }}</p>
        <div class="divider"></div>
        <div v-loading="loading">
            <div class="content" v-html="content">
                
            </div>

            <mavon-editor
                class="markdown-edit"
                v-model="markdown" 
                @change="updateDoc" 
                :toolbars="toolbars"
                :subfield="false" 
                :defaultOpen="preview"/>

        </div>
    </div>
</template>

<script>
const toolbars = {
    bold: true, // 粗体
    italic: true, // 斜体
    header: true, // 标题
    underline: true, // 下划线
    strikethrough: true, // 中划线
    mark: true, // 标记
    superscript: true, // 上角标
    subscript: true, // 下角标
    quote: true, // 引用
    ol: true, // 有序列表
    ul: true, // 无序列表
    link: true, // 链接
    imagelink: true, // 图片链接
    code: true, // code
    table: true, // 表格
    fullscreen: false, // 全屏编辑
    readmodel: false, // 沉浸式阅读
    htmlcode: false, // 展示html源码
    help: false, // 帮助
    /* 1.3.5 */
    undo: true, // 上一步
    redo: true, // 下一步
    trash: true, // 清空
    save: false, // 保存（触发events中的save事件）
    /* 1.4.2 */
    navigation: false, // 导航目录
    /* 2.1.8 */
    alignleft: true, // 左对齐
    aligncenter: true, // 居中
    alignright: true, // 右对齐
    /* 2.2.1 */
    subfield: false, // 单双栏模式
    preview: false // 预览
};


export default {
    name: 'Archives',
    data() {
        return {
            title: '',
            content: '',
            markdown: '',
            toolbars,
            loading: true
        }
    },
    methods: {
        updateDoc(value, render) {
            this.content = render;
        }
    },
    created() {
        this.$emit('tooltip', {
            title: 'Back to Index',
            routeName: 'Index'
        });

        this.loading = true;
        this.$axios({
            method: 'GET',
            url: this.serverAddress + '/archives/archives?id=' + this.$route.params.id
        }).then((response) => {
            let info = response.data;
            let status = info.status;
            switch(status) {
                case 200:
                    this.title = info.data.title;
                    this.content = info.data.content;
                    this.loading = false;
                    break;
                case 404:
                    this.title = "404 空的~";
                    this.content = "<center>这里没有你想要的</center>";
                    break;

            }   
        });
    }
}
</script>

<style scoped>
    .title {
        margin: 0;
        font-size: 20px;
    }

    .content {
        margin: 0 auto;
        max-width: 450px;
        text-align: left;
    }

    .markdown-edit {
        margin: 0 auto;
        margin-top: 30px;
        max-width: 600px;
    }
</style>


